"use client"

import { Card } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { ScrollArea } from "@/components/ui/scroll-area"
import { useState } from "react"

// Mock data for demonstration
const mockClients = [
  { id: 1, name: "John Doe", email: "john@example.com", riskScore: 7.5, portfolioValue: "$150,000" },
  { id: 2, name: "Jane Smith", email: "jane@example.com", riskScore: 5.2, portfolioValue: "$280,000" },
  { id: 3, name: "Mike Johnson", email: "mike@example.com", riskScore: 8.1, portfolioValue: "$420,000" },
]

const mockTrades = [
  { id: 1, client: "John Doe", type: "BUY", asset: "AAPL", amount: "$5,000", status: "Completed" },
  { id: 2, client: "Jane Smith", type: "SELL", asset: "GOOGL", amount: "$8,000", status: "Pending" },
  { id: 3, client: "Mike Johnson", type: "BUY", asset: "MSFT", amount: "$12,000", status: "Completed" },
]

const mockFunds = [
  { id: 1, client: "John Doe", type: "Deposit", amount: "$10,000", status: "Approved" },
  { id: 2, client: "Jane Smith", type: "Withdrawal", amount: "$5,000", status: "Pending" },
  { id: 3, client: "Mike Johnson", type: "Deposit", amount: "$25,000", status: "Approved" },
]

export default function AdminDashboard() {
  const [searchTerm, setSearchTerm] = useState("")

  const filteredClients = mockClients.filter(client =>
    client.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    client.email.toLowerCase().includes(searchTerm.toLowerCase())
  )

  return (
    <div className="container mx-auto p-6 space-y-8">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold tracking-tight">Admin Dashboard</h1>
        <Button>Generate Report</Button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card className="p-6">
          <h3 className="font-semibold mb-2">Total Clients</h3>
          <p className="text-3xl font-bold">{mockClients.length}</p>
        </Card>
        <Card className="p-6">
          <h3 className="font-semibold mb-2">Active Trades</h3>
          <p className="text-3xl font-bold">{mockTrades.length}</p>
        </Card>
        <Card className="p-6">
          <h3 className="font-semibold mb-2">Pending Fund Requests</h3>
          <p className="text-3xl font-bold">{mockFunds.filter(f => f.status === "Pending").length}</p>
        </Card>
      </div>

      <Tabs defaultValue="clients" className="w-full">
        <TabsList>
          <TabsTrigger value="clients">Client Database</TabsTrigger>
          <TabsTrigger value="trades">Trade Log</TabsTrigger>
          <TabsTrigger value="funds">Fund Flow</TabsTrigger>
        </TabsList>

        <TabsContent value="clients" className="space-y-4">
          <div className="flex justify-between items-center">
            <Input
              placeholder="Search clients..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="max-w-sm"
            />
            <Button variant="outline">Export Data</Button>
          </div>
          
          <ScrollArea className="h-[400px] rounded-md border">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Name</TableHead>
                  <TableHead>Email</TableHead>
                  <TableHead>Risk Score</TableHead>
                  <TableHead>Portfolio Value</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredClients.map((client) => (
                  <TableRow key={client.id}>
                    <TableCell>{client.name}</TableCell>
                    <TableCell>{client.email}</TableCell>
                    <TableCell>{client.riskScore}</TableCell>
                    <TableCell>{client.portfolioValue}</TableCell>
                    <TableCell>
                      <Button variant="ghost" size="sm">View</Button>
                      <Button variant="ghost" size="sm">Edit</Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </ScrollArea>
        </TabsContent>

        <TabsContent value="trades">
          <ScrollArea className="h-[400px] rounded-md border">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Client</TableHead>
                  <TableHead>Type</TableHead>
                  <TableHead>Asset</TableHead>
                  <TableHead>Amount</TableHead>
                  <TableHead>Status</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {mockTrades.map((trade) => (
                  <TableRow key={trade.id}>
                    <TableCell>{trade.client}</TableCell>
                    <TableCell>{trade.type}</TableCell>
                    <TableCell>{trade.asset}</TableCell>
                    <TableCell>{trade.amount}</TableCell>
                    <TableCell>{trade.status}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </ScrollArea>
        </TabsContent>

        <TabsContent value="funds">
          <ScrollArea className="h-[400px] rounded-md border">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Client</TableHead>
                  <TableHead>Type</TableHead>
                  <TableHead>Amount</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {mockFunds.map((fund) => (
                  <TableRow key={fund.id}>
                    <TableCell>{fund.client}</TableCell>
                    <TableCell>{fund.type}</TableCell>
                    <TableCell>{fund.amount}</TableCell>
                    <TableCell>{fund.status}</TableCell>
                    <TableCell>
                      {fund.status === "Pending" && (
                        <>
                          <Button variant="ghost" size="sm">Approve</Button>
                          <Button variant="ghost" size="sm">Reject</Button>
                        </>
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </ScrollArea>
        </TabsContent>
      </Tabs>
    </div>
  )
}
